/*
 * RovenBlog Dashboards notifications
 */
 
/* =Hero Section
   ========================================================================== */

	.rb-dashboard-hero {
		background-color: #ffffff;
		border-radius: 8px;
		padding: 15px 15px 0;
		margin-bottom: 20px;
	}

	.notice .rb-dashboard-hero {
		padding: 15px 15px 0;
		margin-bottom: -5px;
		border-radius: 0;
	}

	/*
	 * 1. Clearfix hack 
	 */
	.rb-dashboard-hero:after { /* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}

	.rb-dashboard-hero-content-left {}

	.rb-dashboard-hero-greating {
		color: #1e1e1e;
		font-size: 1rem;
		font-weight: 600;
		margin-bottom: 15px;
		line-height: 1.5;
	}

	.rb-dashboard-hero-title {
		color: #1e1e1e;
		font-size: 1.75rem;
		line-height: 1.3;
		font-weight: 600;
		margin-bottom: 15px;
	}

	.rb-dashboard-hero-description {
		margin-bottom: 15px;
		line-height: 1.5;
	}

	.rb-dashboard-hero-actions {
		margin-bottom: 15px;
	}

	.rb-dashboard-hero-content-right { 
		
	}

	.rb-dashboard-hero-content-right img {
		max-width: 100%;
		display: block;
	} 

	@media (min-width: 783px) {

		.notice .rb-dashboard-hero {
			margin-bottom: -1px;
		}

	}

	@media (min-width: 1024px) {
		
		.rb-dashboard-hero {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
		
		.rb-dashboard-hero-content-left {
			width: 55%;
		}
		
		.rb-dashboard-hero-content-right { 
			width: 40%;
		}
		
	}

	@media (min-width: 1400px) {
		
		.rb-dashboard-hero {
			padding: 30px 30px 0;
			margin-bottom: 20px;
		}
		
	}

/* =Category Thumbnails
   ========================================================================== */

	.rb_preview_image {
		max-height: 120px;
		background-color: rgba(0,0,0,0.15);
	}